.Label {
    display: flex;
    flex-direction: column;
    margin-bottom: 10px;
  }
  
  .Form {
    display: flex;
    width: 250px;
    margin: 0 auto;
    padding: 20px;
    justify-content: center;
    border: 1px solid #000;
  }
  
  .Input {
    padding: 10px;
    border-radius: 10px;
    border: 1px solid #000;
  }
  
  .Input:hover,
  .Input:focus {
    border:1px solid #000;
  }
  
  .NameBtn {
    padding: 5px 15px;
    margin: 10px;
    border: 1px solid #000;
    background-color: #fff;
    border-radius: 10px;
    cursor: pointer;
  }
  
  .NameBtn:hover,
  .NameBtn:focus {
    background-color: rgb(128, 166, 255);
  }